import { combineReducers } from "redux";
import { packagesReducer } from "./ducks/packages/reducer";
import { userReducer } from "./ducks/user/reducer";

import { packageReducer } from "./ducks/package/reducer";
import { notificationReducer } from "./ducks/notification/reducer";
import { modalReducer } from "./ducks/modal/reducer";
import { destionationsReducer } from "./ducks/destinations/reducer";

import { destinationReducer } from "./ducks/destination/reducer";


export const rootReducer = combineReducers({
  User: userReducer,
  Packages: packagesReducer,
  Package: packageReducer,
  Notification: notificationReducer,
  Modal: modalReducer,
  Destionations: destionationsReducer,
  Destionation: destinationReducer
})