import { useForm } from "react-hook-form";
import SignIn from "..";

import * as yup from "yup";
import { yupResolver } from "@hookform/resolvers/yup";
import { Link } from "react-router-dom";
import { useDispatch } from "react-redux";
import { fetchSignIn } from "../../../store/ducks/user/actionCreators";
import { setModalStatus } from "../../../store/ducks/modal/actionCreators";



const FormSchema = yup.object().shape({
  username: yup.string().required(),
  email: yup.string().email().required(),
  mobile: yup.string().required(),
  password: yup.string().required()
})



const CreateProfile = () => {

  const dispatch = useDispatch();
  const { register, handleSubmit, errors} = useForm({
    resolver: yupResolver(FormSchema)
  });



  const onSubmit = (data) => {
    console.log(data)

    dispatch(fetchSignIn(data))
  }

  return (
    <>
      <div className="login pl-5 pr-5">
        <h1 className="login__title pt-3 pb-4">
          <span >
            Signup
          </span>
        </h1>
        <div>
        <div className="mobile">
          <form onSubmit={handleSubmit(onSubmit)}>
            <div className="pb-3">
              <b>Name</b>
              <div className="mt-1">
                <input ref={register} name="username" id="username" className="mobile__input" type="text" placeholder="Name"/>
              </div>
              <small className="text-danger">{errors.username?.message}</small>
            </div>
            <div className="pb-3">
              <b>Email</b>
              <div className="mt-1">
                <input ref={register} name="email" id="email" className="mobile__input" type="text" placeholder="Email"/>
              </div>
              <small className="text-danger">{errors.email?.message}</small>
            </div>
            <div className="pb-3">
              <b>Mobile</b>
              <div className="mt-1">
                <input ref={register} name="mobile" id="mobile" className="mobile__input" type="text" placeholder="Moile Number"/>
              </div>
              <small className="text-danger">{errors.mobile?.message}</small>
            </div>
            <div className="pb-3">
              <b>Password</b>
              <div className="mt-1">
                <input ref={register} name="password" id="password" className="mobile__input" type="password" placeholder="Password"/>
              </div>
              <small className="text-danger">{errors.password?.message}</small>
            </div>
            <button type="submit" className="mobile__btn mt-2" >Submit</button>
          </form>
          <p className="mt-2">Go To Login ? <Link onClick={() => dispatch(setModalStatus('login'))}>Click here</Link></p>
        </div>
          
        </div>
      </div>
    </>
  );
}
 
export default CreateProfile;