import { produce } from "immer";
import { ModalActionsType } from "./contracts/actionType";

const modalInitialState = {
  visibal: undefined
}

export const modalReducer = produce((draft, action) => {

  switch(action.type){
    case ModalActionsType.SET_MODAL_STATUS: 
      draft.visibal = action.payload;
      break;
      
    default: 
      break
  }
}, modalInitialState)