import { call, put, takeLatest } from "@redux-saga/core/effects";
import { DestinationApi } from "../../../services/api/destinationApi";
import { PackageApi } from "../../../services/api/packageApi";
import { LoadingStatus } from "../../types";
import { setDestinationData, setLoadingStatus } from "./actionCreator";


import { DestinationActionsType } from "./contracts/actionType";

export function* fetchDestinationRequest({ payload }) {
  try {
    yield put(setLoadingStatus(LoadingStatus.LOADING));
    const data = yield call(DestinationApi.fetchDestination, payload);
    console.log(data)
    yield put(setDestinationData(data));
  } catch (error) {
    yield put(setLoadingStatus(LoadingStatus.ERROR));
  }
}


export function* destinationSaga() {
  yield takeLatest(DestinationActionsType.FETCH_DESTINATION_DATA, fetchDestinationRequest);
}

