import { LoadingStatus } from "../../types";

export const selectPackageState = (state) => state.Package;

export const selectPackageData = (state) => state.Package.data;

export const selectPackageStatus = (state) => selectPackageState(state).status;

export const selectPackageIsLoading = (state) =>  
selectPackageState(state).status === LoadingStatus.LOADING;

export const selectPackageIsLoaded = (state) => 
selectPackageState(state).status === LoadingStatus.LOADED;


