import * as yup from "yup";
import { yupResolver } from "@hookform/resolvers/yup";


export const Profile = () => {

  const { errors, handleSubmit, register} = useForm();

  const onSubmit = (data) => {
    console.log(data)
  }

  return (
    <Layout>
      <TopHeader />
      <Container className="mt-5 pt-3 pb-5">
        <form onSubmit={handleSubmit(onSubmit)}>
          <div className="mt-5 pt-3 pb-5">
            <div className="profile">
            <div className="profile__img mb-4">
              <img src="https://t4.ftcdn.net/jpg/00/64/67/63/360_F_64676383_LdbmhiNM6Ypzb3FM4PPuFP9rHe7ri8Ju.jpg" height="100" width="100" alt="profile image" style={{
                borderRadius: '50%'
              }}/>

              <input className="ml-5 pl-4" type="file" placeholder="upload image" ref={register} name="image" id="image"/>
            </div>
             
            <Col md={7} sm={12}>
              <form>
                <Form.Group as={Row} controlId="formPlaintextEmail">
                  <Form.Label column sm="3">
                    Name
                  </Form.Label>
                  <Col sm="9">
                    <Form.Control size="sm" defaultValue="Prince Dewangan" ref={register} name="name" id="name"/>
                  </Col>
                </Form.Group>
  
                <Form.Group as={Row} controlId="formPlaintextPassword">
                  <Form.Label column sm="3">
                    Email
                  </Form.Label>
                  <Col sm="9">
                    <Form.Control size="sm" type="email" placeholder="dewanganprince02@gmail.com" ref={register} name="email" id="email"/>
                  </Col>
                </Form.Group>
  
                <Form.Group as={Row} controlId="formPlaintextPassword">
                  <Form.Label column sm="3">
                    Mobile Number
                  </Form.Label>
                  <Col sm="9">
                    <Form.Control size="sm" type="text" disabled placeholder="7889562312" />
                  </Col>
                </Form.Group>

                <Form.Group as={Row} controlId="formPlaintextPassword">
                  <Form.Label column sm="3">
                    State
                  </Form.Label>
                  <Col sm="9">
                    <Form.Control size="sm" type="text" placeholder="Chhattisgarh" name="state" id="state" ref={register}/>
                  </Col>
                </Form.Group>

                <Form.Group as={Row} controlId="formPlaintextPassword">
                  <Form.Label column sm="3">
                    City
                  </Form.Label>
                  <Col sm="9">
                    <Form.Control size="sm" type="text"  placeholder="Raipur" name="city" ref={register} id="city"/>
                  </Col>
                </Form.Group>
  
                <Form.Group as={Row} controlId="formPlaintextPassword">
                  <Form.Label column sm="3">
                    Address
                  </Form.Label>
                  <Col sm="9">
                    <Form.Control size="sm" rows={3} as="textarea" placeholder="Address.............." ref={register} id="address" name="address" />
                  </Col>
                </Form.Group>
                <Button type="submit">Submit</Button>
              </form>
            </Col>
          </div>
          </div>
        </form>
      </Container>
    </Layout>
  );
}
 

