
import { LoadingStatus } from "../../types";

export const selectPackagesState = (state) => state.Packages;

export const selectPackagesData = (state) => state.Packages;

export const selectPackagesStatus = (state) => selectPackagesState(state).status;

export const selectPackagesIsLoading = (state) =>  
selectPackagesState(state).status === LoadingStatus.LOADING;

export const selectPackagesIsLoaded = (state) => 
selectPackagesState(state).status === LoadingStatus.LOADED;



