import { LoadingStatus } from "../../types";

export const selectDestinationState = (state) => state.Destionation;

export const selectDestinationData = (state) => state.Destionation.data;

export const selectDestinationStatus = (state) => selectDestinationState(state).status;

export const selectDestinationIsLoading = (state) =>  
selectDestinationState(state).status === LoadingStatus.LOADING;

export const selectDestinationIsLoaded = (state) => 
selectDestinationState(state).status === LoadingStatus.LOADED;


