import produce from "immer";
import { LoadingStatus } from "../../types";
import { PackageActionsType } from "./contracts/actionTypes";


const initialPackageState = {
  data: undefined,
  status: LoadingStatus.NEVER
}
  

export const packageReducer = produce((draft, action) => {
  switch(action.type){
    case PackageActionsType.SET_PACKAGE_DATA: 
      draft.data = action.payload;
      draft.status = LoadingStatus.SUCCESS;
      break;

    case PackageActionsType.FETCH_PACKAGE_DATA:
      draft.data = undefined;
      draft.status = LoadingStatus.LOADING;
      break;

    case PackageActionsType.SET_LOADING_STATE:
      draft.status = action.payload;
      break
    
    default:
      break;
  }
}, initialPackageState)

