import { all } from "@redux-saga/core/effects";
import { packagesSaga } from "./ducks/packages/sagas";

import { packageSaga } from "./ducks/package/sagas";
import { userSaga } from "./ducks/user/sagas";
import { destionationsSaga } from "./ducks/destinations/sagas";
import { destinationSaga } from "./ducks/destination/sagas";



export default function* rootSaga() {
  yield all([ userSaga(), packagesSaga(), packageSaga(), destionationsSaga(), destinationSaga() ])
}