import { LoadingStatus } from "../../types";

export const selectUserState = (state) => state.User;

export const selectUserData = (state) => selectUserState(state).data;

export const selectIsAuth = (state) => !!selectUserState(state).data;

export const selectUserStatus = (state) =>
  selectUserState(state).status;

export const selectUserIsLoading = (state) =>
  selectUserState(state).status === LoadingStatus.LOADING;

export const selectUserIsLoaded = (state) =>
  selectUserState(state).status === LoadingStatus.LOADED;

