import { Button, Modal } from "react-bootstrap";

import React, { useState } from 'react';
import OtpInput from 'react-otp-input';
import { useDispatch, useSelector } from "react-redux";
import { setModalStatus } from "../store/ducks/modal/actionCreators";
import { selectModalData } from "../store/ducks/modal/selectors";
import { fetchVerifyPhoneOtp } from "../store/ducks/user/actionCreators";
 

// import OTPInput, { ResendOTP } from "otp-input-react";
const ModalBlock = () => {

  const dispatch = useDispatch();
  const modal = useSelector(selectModalData);
  const [OTP, setOTP] = useState("");

  const handleChange = otp => {
    setOTP(otp);
  }

  const handleSubmit = () => {
    if(OTP.length === 4){
      dispatch(fetchVerifyPhoneOtp({otp: OTP}))
      dispatch(setModalStatus(undefined))
    }
  }

  return (
    <Modal show={modal === '_otp'} animation onHide={()=>dispatch(setModalStatus('register'))}>
    <Modal.Header closeButton></Modal.Header>
    <Modal.Body>
      <div className="modal__block">
      <h5 className="modal__title mt-2 mb-4">Enter Verification Code</h5> 
      <OtpInput
        containerStyle="container__style"
        inputStyle="input__style"
        value={OTP}
        onChange={handleChange}
        numInputs={4}
        separator={<span>&nbsp;-&nbsp;</span>}
      />
        
    <div className="mt-4 mb-4">
    
    {/* <button className="m-1">Resent otp</button> */}
    <Button size="sm" style={{
      background: '#222',
      border: '1px solid #222'
    }} className="m-1" onClick={handleSubmit}>Submit</Button>
    </div>
    </div>

    </Modal.Body>

    </Modal>
  );
}
 
export default ModalBlock;