import { NotificationActionsType } from "./contracts/actionTypes";

export const setNotificationSuccess = (payload) => ({
   type: NotificationActionsType.SET_NOTIFICATION_SUCCESS,
   payload
});

export const setNotificationError = (payload) => ({
    type: NotificationActionsType.SET_NOTIFICATION_ERROR,
    payload
})

export const clearNotification = () => ({
   type: NotificationActionsType.CLEAR_NOTIFICATION
})