import { axios } from "../../core/axios";

const url = process.env.REACT_APP_ROOT_API;

export const AuthApi = {
  async signIn(postData) {
    const data = await axios.post(`${url}api/user/register`,postData);
    return data;
  },
  async googleSignIn(postData) {
    const data = await axios.post(`${url}api/user/googlelogin`,postData);
    return data;
  },
  async facebookSignIn(postData) {
    const data = await axios.post(`${url}api/user/facebooklogin`,postData);
    return data;
  },
  async signUp() {

  },
  async getMe() {
    return {
      'name': 'prince dewangan',
      'mobile': '7000096883',
      'location': 'raipur'
    }
    // const data = await axios.get(`${url}api/user/inside/profileView`);
    // return data;
  },
  async phoneOtp(postData) {
    const data = await axios.post(`${url}api/user/mobile/otp/login`,postData);
    return data;
  },
  async phoneVerify(postData) {
    const data = await axios.post(`${url}api/user/mobile/otp/verify/${localStorage.getItem("phone")}`,postData);
    return data
  }
}



