import Snackbar from '@material-ui/core/Snackbar';
import Alert, { Color } from '@material-ui/lab/Alert';
import { useDispatch, useSelector } from 'react-redux';


import { clearNotification } from "../store/ducks/notification/actionCreators";
import { selectNotificationState  } from "../store/ducks/notification/selectors";

const Notification = () => {

  const dispatch = useDispatch();
  const obj = useSelector(selectNotificationState);


  return (
    <Snackbar autoHideDuration={3000} anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}  open={obj.visibal} autoHideDuration={6000} onClose={() => dispatch(clearNotification())}>
      <Alert onClose={() => dispatch(clearNotification())} 
        severity={obj?.type}
      >
        {obj?.text}
      </Alert>
  </Snackbar>

  );
}
 
export default Notification;

