

import { DestinationsActionType } from "./contracts/actionTypes";

export const fetchDestinationsPackages = () => ({
  type: DestinationsActionType.FETCH_DESTINATION_PACKAGES
})

export const setDestinationsPackagesData = (payload) => ({
  type: DestinationsActionType.SET_DESTINATION_PACKAGES_DATA,
  payload
})
  
export const setLoadingStatus = (payload) => ({
  type: DestinationsActionType.SET_LOADING_STATE,
  payload
})