import { DestinationActionsType } from "./contracts/actionType";


export const fetchDestinationData = (payload) => ({
  type: DestinationActionsType.FETCH_DESTINATION_DATA,
  payload
})

export const setDestinationData = (payload) => ({
  type: DestinationActionsType.SET_DESTINATION_DATA,
  payload
})
  
export const setLoadingStatus = (payload) => ({
  type: DestinationActionsType.SET_LOADING_STATE,
  payload
})
    
    