
import { LoadingStatus } from "../../types";

export const selectDestionationsState = (state) => state.Destionations;

export const selectDestionationsData = (state) => state.Destionations;

export const selectDestionationsStatus = (state) => selectDestionationsState(state).status;

export const selectDestionationsIsLoading = (state) =>  
selectDestionationsState(state).status === LoadingStatus.LOADING;

export const selectDestionationsIsLoaded = (state) => 
selectDestionationsState(state).status === LoadingStatus.LOADED;



