import React, { useEffect, useState } from "react";
import SignIn from "..";

import GoogleLogin from 'react-google-login';
import { useDispatch, useSelector } from 'react-redux';

import { fetchGoogleSignIn, fetchPhoneOtp, fetchVerifyPhoneOtp } from "../../../store/ducks/user/actionCreators";
import { Container, Button } from "react-bootstrap";
import { Link, useHistory } from "react-router-dom";
import { useForm } from "react-hook-form";

import * as yup from "yup";
import { yupResolver } from "@hookform/resolvers/yup";
import ModalBlock from "../../../components/ModalBlock";

import { selectIsAuth, selectUserStatus } from "../../../store/ducks/user/selectors";

import { setModalStatus } from "../../../store/ducks/modal/actionCreators";
import { LoadingStatus } from "../../../store/types";
import { selectModalData } from "../../../store/ducks/modal/selectors";
import OtpInput from "react-otp-input";



const Schema = yup.object().shape({
  mobile: yup.string().required()
})

const SocialMediaLogin = () => {
  
  const dispatch = useDispatch();
  const isAuth = useSelector(selectIsAuth);
  const loadingStatus = useSelector(selectUserStatus);
  const history = useHistory();

  const [show, setShow] = useState(false)

  const [dis, setDis] = useState(false);
  const { errors, handleSubmit, register} = useForm({
    resolver: yupResolver(Schema)
  });

  const overrideStyle = () => {
    // let btn = document.getElementsByClassName('loginBtn');
    // btn[0].style={}
    // btn[0].innerHTML=`<small>Login with Google</small>`
  }

  useEffect(() => {
    overrideStyle()
  },[])


  useEffect(() => {
    if(isAuth){
      history.push('/home')
    }
  },[isAuth])


  const responseGoogle = async (data) => {
    console.log(data)
    dispatch(fetchGoogleSignIn(data.tokenId));
  }




  const onSubmit = (data) => {
    setDis(true)
    localStorage.setItem("phone",data.mobile)
    dispatch(fetchPhoneOtp(data))
    setShow(!show)
  }


  const modal = useSelector(selectModalData);
  const [OTP, setOTP] = useState("");

  const handleChange = otp => {
    setOTP(otp);
  }

  const _handleSubmit = () => {
    if(OTP.length === 4){
      dispatch(fetchVerifyPhoneOtp({otp: OTP}))
      dispatch(setModalStatus(undefined))
    }
  }


  return (
    <>
      {/* <Container> */}
      <div className="login pt-4 pl-5 pr-5 pb-5">
        <h1 className="login__title  pb-4">
          <span >
            Login 
          </span>
        </h1>
        <div>
          {/* <GoogleLogin  className="loginBtn loginBtn--google btn_block"
            clientId="813545121860-3p28j8rhkbj7jit3vi0rc5pvi8uqa1t5.apps.googleusercontent.com"
            buttonText="Login with Google"
            onSuccess={responseGoogle}
            onFailure={responseGoogle}
            cookiePolicy={'single_host_origin'}
          /> */}
          {/* <button class="loginBtn loginBtn--facebook">
            Login with Facebook
          </button> */}
        </div>
        {/* <div className="login__other pt-4 pb-4">
          or <hr/>
        </div> */}

        <div className="mobile">
          <form onSubmit={handleSubmit(onSubmit)}>
            <b>Enter mobile number</b>
            <div className="mt-2">
              <input ref={register} name="mobile" id="mobile" 
                className="mobile__input"
                type="text" placeholder="Phone Number"/>
            </div>
            <div>
            <small className="text-danger">{errors.mobile?.message}</small>
            </div>
            <button 
              // disabled={loadingStatus === LoadingStatus.LOADING} 
              type="submit" disabled={dis} className="mobile__btn mt-3" >Get OTP</button>
          </form>
        </div>
        <p className="mt-5">Create New account ? <Link onClick={() => dispatch(setModalStatus('register'))}>Click here</Link></p>
      </div>
{show?
      <div className="modal__block">
      <h5 className="modal__title mt-2 mb-4">Enter Verification Code</h5> 
      <OtpInput
        containerStyle="container__style"
        inputStyle="input__style"
        value={OTP}
        onChange={handleChange}
        numInputs={4}
        separator={<span>&nbsp;-&nbsp;</span>}
      />
        
    <div className="mt-4 mb-4">
    
    {/* <button className="m-1">Resent otp</button> */}
    <Button size="sm" style={{
      background: '#222',
      border: '1px solid #222'
    }} className="m-1" onClick={_handleSubmit}>Submit</Button>
    </div>
    </div> : null}





      <ModalBlock/>
      {/* </Container> */}


      {/* <div className="mt-3 d-flex">
        <GoogleLogin  className="loginBtn loginBtn--google btn_block"
          clientId="813545121860-3p28j8rhkbj7jit3vi0rc5pvi8uqa1t5.apps.googleusercontent.com"
          buttonText="Login with Google"
          onSuccess={responseGoogle}
          onFailure={responseGoogle}
          cookiePolicy={'single_host_origin'}
        />
        <button class="loginBtn loginBtn--facebook">
          Login with Facebook
        </button>
      </div> */}
      {/* <p className="section__right-text">Lorem ipsum dolor sit amet consectetur adipisicing elit. Quaerat sunt blanditiis ipsam recusandae.</p> */}
    
    
    </>
  );
}
 
export default SocialMediaLogin;