
import { call, put, takeLatest } from "redux-saga/effects";
import { PackagesActionType } from "./contracts/actionTypes";

import { setLoadingStatus, setPackagesData } from "./actionCreators";

import { PackagesApi } from "../../../services/api/packagesApi";
import { LoadingStatus } from "../../types";


export function* fetchPackagesRequest() {
  try {
    yield put(setLoadingStatus(LoadingStatus.LOADING));
    const data = yield call(PackagesApi.fetchPackages);
    yield put(setPackagesData(data));
  } catch (error) {
    yield put(setLoadingStatus(LoadingStatus.ERROR));
  }
}



export function* packagesSaga() {
  yield takeLatest(PackagesActionType.FETCH_PACKAGES, fetchPackagesRequest)
}