
import { call, put, takeLatest } from "redux-saga/effects";
import { setUserLoadingStatus, setUserData } from "./actionCreators";
import { UserActionsType } from "./contracts/actionTypes";

import { LoadingStatus } from "../../types";
import { AuthApi } from "../../../services/api/authApi";
import { setNotificationError, setNotificationSuccess } from "../notification/actionCreators";
import { setModalStatus } from "../modal/actionCreators";


export function* fetchSignInRequest({ payload }){
  try {
    yield put(setUserLoadingStatus(LoadingStatus.LOADING));
    const data = yield call(AuthApi.signIn, payload);
    console.log(data)
    yield put(setUserLoadingStatus(LoadingStatus.LOADED));
    yield put(setNotificationSuccess('Registered successfully!'));
  } catch (error) {
    yield put(setUserLoadingStatus(LoadingStatus.ERROR));
    yield put(setNotificationError('Failed to register!'))
  }
}

export function* fetchUserDataRequest(){
  try {
    // yield put(setUserLoadingStatus(LoadingStatus.LOADING));
    const data = yield call(AuthApi.getMe);
    
    yield put(setUserLoadingStatus(LoadingStatus.SUCCESS));
    console.log(data)
  } catch (error) {
    yield put(setUserLoadingStatus(LoadingStatus.ERROR));
  }
}

