export function* fetchPhoneOtpRequest({ payload }) {
  try {
    // yield put(setUserLoadingStatus(LoadingStatus.LOADING));
    const data = yield call(AuthApi.phoneOtp, payload);
    // yield put(setModalStatus('otp'));
    console.log(data)
    //show show notification
    yield put(setNotificationSuccess('OTP sent to your mobile number'));
  } catch (error) {
    //show show notification error
    // yield put(setUserLoadingStatus(LoadingStatus.ERROR));
    yield put(setNotificationError('Error while sending OTP'));
  }
} 

export function* fetchVerifyPhoneOtpRequest({payload}) {
  try {
    yield put(setUserLoadingStatus(LoadingStatus.LOADING));
    const {data} = yield call(AuthApi.phoneVerify,payload);
    console.log(data)
    localStorage.setItem('token',data.token);
    yield put(setUserData(data))
    yield put(setNotificationSuccess('Logged in successfully!'));
  } catch (error) {
    yield put(setUserLoadingStatus(LoadingStatus.ERROR));
    yield put(setNotificationError('Some error occured!'));
  }
}


