export function* userSaga() {
  yield takeLatest(UserActionsType.FETCH_SIGN_IN, fetchSignInRequest);
  yield takeLatest(UserActionsType.FETCH_SIGN_UP, fetchSignUpRequest);
  yield takeLatest(UserActionsType.FETCH_USER_DATA, fetchUserDataRequest);
  // yield takeLatest(UserActionsType.FETCH_GOOGLE_SIGN_IN, fetchGoogleSignInRequest);
  yield takeLatest(UserActionsType.FETCH_FACEBOOK_SIGN_IN, fetchFacebookSignInRequest);
  yield takeLatest(UserActionsType.FETCH_PHONE_OTP, fetchPhoneOtpRequest);
  yield takeLatest(UserActionsType.FETCH_VERIFY_PHONE_OTP, fetchVerifyPhoneOtpRequest);
}