import Header from "./components/Header";
import TopHeader from "./components/TopHeader";


import bg from "./assets/bg_12.jpg";
import Section from "./components/Section";
import Layout from "./pages/Layout";
import SignIn from "./pages/SignIn";
import Home from "./pages/Home";
import Camping from "./pages/Camping";
import TribalHomeStays from "./pages/TribalHomeStays";
import React from "react";
import { Route, Switch, useHistory } from "react-router";
import SaveDestination from "./pages/SaveDestination";

import SocialMediaLogin from "./pages/SignIn/component/SocialMediaLogin";
import PhoneLogin from "./pages/SignIn/component/PhoneLogin";
import Notification from "./components/Notification";


import PackageDetails from "./pages/PackageDetails/";

import { Profile, ProfileGmail, ProfileOtp } from "../src/pages/Profile";
import Enquiry from "./pages/Enquiry";
import CreateProfile from "./pages/SignIn/component/CreateProfile";
import { useDispatch, useSelector } from "react-redux";
import { selectIsAuth, selectUserStatus } from "./store/ducks/user/selectors";
import { LoadingStatus } from "./store/types";
import Loader from "react-loader-spinner";
import { fetchUserData } from "./store/ducks/user/actionCreators";
import Visitors from "./pages/Visitors";
import Operators from "./pages/Operators";
import DestinationDetails from "./pages/DestinationDetails/DestinationDetails";
import Search from "./pages/Search";


import LaunchModal from "./components/LaunchModal";

import Fab from '@material-ui/core/Fab';


import whapp from "./assets/whatsapp.svg";



import EditIcon from '@material-ui/icons/Edit';
import { makeStyles } from "@material-ui/core";
import { Hidden } from "@material-ui/core";
import { setModalStatus } from "./store/ducks/modal/actionCreators";


const useStyles = makeStyles((theme) => ({
  root: {
    '& > *': {
      margin: theme.spacing(1),
    },
  },
  extendedIcon: {
    marginRight: theme.spacing(1),
  },
}));

function App() {

  const classes = useStyles();
  const history = useHistory();
  const dispatch = useDispatch();
  const isAuth = useSelector(selectIsAuth);
  const loadingStatus = useSelector(selectUserStatus);
  const isReady = loadingStatus !== LoadingStatus.NEVER && loadingStatus !== LoadingStatus.LOADING;

  React.useEffect(() => {
    dispatch(fetchUserData());
    dispatch(setModalStatus('launch'))
  }, [dispatch]);


  // React.useEffect(() => {
    
  //   if(!isAuth && isReady){
  //     history.push('/signin')
  //   } else if(history.location.pathname === '/'){
  //     history.push('/home')
  //   }
  //   // eslint-disable-next-line react-hooks/exhaustive-deps
  // }, [isAuth, isReady]);

  if (!isReady) {
    return (
      <div className="loader_center">
       <Loader 
         type="Circles"
         color="#b8b4fc"
         height={40}
         width={70}
         timeout={3000}
       />
      </div>
    );
  }

  
  return (
    <React.Fragment>
  
      <Notification />
     {/* <h1> {JSON.stringify(isAuth)} ---{JSON.stringify(isReady)}</h1> */}
      <Switch>
        <Route path="/signin" component={SocialMediaLogin} exact/>
        <Route path={["/", "/home"]} component={Home} exact/>
        <Route path="/camping" component={Camping}/>
        <Route path="/tribal_home_stays" component={TribalHomeStays}/>
        <Route path="/save_destination" component={SaveDestination}/>
        <Route path="/package_details/:id" component={PackageDetails}/>
        <Route path="/destination_details/:id" component={DestinationDetails}/>
        <Route path="/enquiry" component={Enquiry}/>
        <Route path="/profile" component={Profile}/>
        <Route path="/profile_m" component={ProfileOtp}/>
        <Route path="/profile_g" component={ProfileGmail}/>
        <Route path="/for-visitors"component={Visitors} exact/>
        <Route path="/for-operators" component={Operators} exact/>
        <Route path="/signup" component={CreateProfile}/>
        <Route path="/phone_no" component={PhoneLogin}/>
        <Route path="/search" component={Search}/>
      </Switch>
      <div className="material__btn">
      <Fab variant="extended" style={{background: '#7868e6'}}>
        
       
        <a href={`https://api.whatsapp.com/send/?phone=+916267020580&text&app_absent=0`} className="web__whatsapp" target="_blank">        
        
        <Hidden only={['sm', 'xs']}>
        <img src={whapp} height="30" width="60"/>
        </Hidden>
          
          <span className="text-white" style={{
            fontSize: 17,
            textTransform: 'capitalize'
          }}>  Tourism Helpline</span>
        </a>


      </Fab>
      </div>
    </React.Fragment>
  );
}

export default App;
