import { call, put, takeLatest } from "@redux-saga/core/effects";
import { PackageApi } from "../../../services/api/packageApi";
import { LoadingStatus } from "../../types";


import { setLoadingStatus, setPackageData } from "./actionCreators";
import { PackageActionsType } from "./contracts/actionTypes";

export function* fetchPackageRequest({ payload }) {
  try {
    yield put(setLoadingStatus(LoadingStatus.LOADING));
    const data = yield call(PackageApi.fetchPackage, payload);
    yield put(setPackageData(data));
  } catch (error) {
    yield put(setLoadingStatus(LoadingStatus.ERROR));
  }
}


export function* packageSaga() {
  yield takeLatest(PackageActionsType.FETCH_PACKAGE_DATA, fetchPackageRequest);
}

