import axios from "axios";
import { useEffect, useState } from "react";
import { Container } from "reactstrap";

import Geocode from "react-geocode";

import Seach from "../assets/search_new.png";

import useGoogle from "react-google-autocomplete/lib/usePlacesAutocompleteService";
import { useHistory } from "react-router-dom";


import { BsSearch } from "react-icons/bs";

import Search_ from "../assets/magnifying-glass.png";
import { Link } from "@material-ui/core";

Geocode.setApiKey(process.env.REACT_APP_GOOGLE_API);

Geocode.setLanguage("en");
Geocode.setRegion("in");

Geocode.setLocationType("ROOFTOP");

// Enable or disable logs. Its optional.
Geocode.enableDebug();


const SearchField = () => {

  const [getLocation, setGetLocation] = useState('');
  const [placeSuggestion, setPlaceSuggestion] = useState([]);
  const history = useHistory();

  let item = []
  const {
    placePredictions,
    getPlacePredictions,
    isPlacePredictionsLoading,
  } = useGoogle({
    apiKey: process.env.REACT_APP_GOOGLE_API,
  });


  const getAddressOfCurrentLocation = async (arg) => {
    Geocode.fromLatLng(arg.lat, arg.lon).then(
      (response) => {
        const address = response.results[0].formatted_address;
        setGetLocation(address);
      },
      (error) => {
        console.error(error);
      }
    );

  }

  const getCurrentLocation = async () => {
    await window.navigator.geolocation.getCurrentPosition(
      pos => {
        console.log(pos);
        getAddressOfCurrentLocation({
          lat: pos.coords.latitude,
          lon: pos.coords.longitude
        });
      }
    )
  }


  useEffect(() => {getCurrentLocation()},[])

  return (
    <Container className="pt-5 _container">
      <div className="search mt-5 pt-5">
        <h1 className="search__title pt-5">
          Near You
        </h1>
    
        {/* <button className="search__btn" onClick={() => history.push('/search')}>
          <img src={Seach} height="25"/> Search destinations 
        </button> */}







        <div className="search__inner" onClick={() => history.push('/search')}>
           <div className="search__block">
            <div className="block__location">
              <label className="block--text code">Search destinations</label>
              {/* <input type="text" className="input--text" 
                value={getLocation} placeholder="Search by City, neighbourhood, etc"
                onChange={(evt) => {
                  setGetLocation(evt.target.value)
                  getPlacePredictions({ input: evt.target.value });
                  console.log(placePredictions);
                  item = []
                  placePredictions.map(sug => {
                    item.push(sug.description)
                  })
                  setPlaceSuggestion(item)
                }}
              /> */}
               {/* <ul class="suggestions ">----------
                {placeSuggestion.map(i=> (
                  <li>{i}</li>
                ))}
              </ul> */}
           
            </div>
            {/* <div className="line"></div>
            <div className="block__query">
              <label className="block--text">Query</label>
              <input type="text" className="input--text" placeholder="Waterfall, Caves, Palaces, etc."/>
            
               <ul class="suggestions"> Suggestion</ul>----------
             
            </div> */}
            <button className="search__btn">
            <BsSearch 
                  // value={{ color: 'blue', size: '1000px' }}
                  color="white"
                  size="25px"
                /> 
              
              </button>
          </div> 
        </div>

      </div>
    </Container>
  );
}
 
export default SearchField;