
import { call, put, takeLatest } from "redux-saga/effects";
import { DestinationsActionType } from "./contracts/actionTypes";

import { setDestinationsPackagesData, setLoadingStatus } from "./actionCreators";

import { LoadingStatus } from "../../types";
import { DestionationsApi } from "../../../services/api/destionationsApi";


export function* fetchDestionationsPackagesRequest() {
  try {
    yield put(setLoadingStatus(LoadingStatus.LOADING));
    debugger
    const data = yield call(DestionationsApi.fetchDestionations);
    yield put(setDestinationsPackagesData(data));
  } catch (error) {
    yield put(setLoadingStatus(LoadingStatus.ERROR));
  }
}



export function* destionationsSaga() {
  yield takeLatest(DestinationsActionType.FETCH_DESTINATION_PACKAGES, fetchDestionationsPackagesRequest)
}