
import produce from "immer";
import { LoadingStatus } from "../../types";
import { PackagesActionType } from "./contracts/actionTypes";

const initialPackagesState = {
  data: [],
  status: LoadingStatus.NEVER
}



export const packagesReducer = produce((draft, action) => {
  switch(action.type){
    case PackagesActionType.SET_PACKAGES_DATA: 
      draft.data = action.payload;
      draft.status = LoadingStatus.SUCCESS;
      break;

    case PackagesActionType.SET_LOADING_STATE:
      draft.status = action.payload;
      break;
    
    default:
      break;
  }
}, initialPackagesState)

