import { PackageActionsType } from "./contracts/actionTypes";


export const fetchPackageData = (payload) => ({
  type: PackageActionsType.FETCH_PACKAGE_DATA,
  payload
})

export const setPackageData = (payload) => ({
  type: PackageActionsType.SET_PACKAGE_DATA,
  payload
})
  
export const setLoadingStatus = (payload) => ({
  type: PackageActionsType.SET_LOADING_STATE,
  payload
})
    
    