import SignIn from "..";

import { Form, Button, InputGroup } from "react-bootstrap";
import PhoneIcon from '@material-ui/icons/Phone';
import ModalBlock from "../../../components/ModalBlock";
import { useForm } from "react-hook-form";


import * as yup from "yup";
import { yupResolver } from '@hookform/resolvers/yup';
import { useDispatch } from "react-redux";
import { fetchPhoneOtp } from "../../../store/ducks/user/actionCreators";
import { useState } from "react";


const Schema = yup.object().shape({
  phone: yup.string().required('Enter Mobile.')
})


const PhoneLogin = () => {

  const dispatch = useDispatch();
  const [bool, setBool] = useState(false)
  const { errors , handleSubmit, register} = useForm({
    resolver: yupResolver(Schema)
  });

  const onSubmit = (data) => {
    console.log(data)
    setBool(true)
    dispatch(fetchPhoneOtp(data))
  }

  return (
    <SignIn>
      <form style={{
          width: '320px'
        }} onSubmit={handleSubmit(onSubmit)}>
        <h5 className="mt-5 mb-3">
          <b>Enter Your Mobile Number</b>
        </h5>
        <InputGroup className="mb-4 mt-2">
          <InputGroup.Prepend>
            <InputGroup.Text id="basic-addon1">
              <PhoneIcon />
            </InputGroup.Text>
          </InputGroup.Prepend>
          <Form.Control
            placeholder="Phone Number"
            aria-label="Username"
            aria-describedby="basic-addon1"
            ref={register}
            name="phone"
            id="phone"
          />
        </InputGroup>
        <small className="text-denger">{errors.phone?.message}</small>
        <Button type="submit" size="sm" block>Contibue</Button>
      </form>
      <ModalBlock visibale={true}/>
    </SignIn>
  );
}


//@1.3.5
 
export default PhoneLogin;