import { Button, Modal } from "react-bootstrap";

import React, { useState } from 'react';

import Launch from "../assets/Website launch banner.jpg";
import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setModalStatus } from "../store/ducks/modal/actionCreators";
import { selectModalData } from "../store/ducks/modal/selectors";

const LaunchModal = () => {


  const modal = useSelector(selectModalData);

  const dispatch = useDispatch();

  return (
      <Modal size="lg" show={modal === "launch"} animation centered
      >
        <img src={Launch} onClick={() => dispatch(setModalStatus(undefined))}/>
    </Modal>
  );
}
 
export default LaunchModal;