

import { PackagesActionType } from "./contracts/actionTypes";

export const fetchPackages = () => ({
  type: PackagesActionType.FETCH_PACKAGES
})

export const setPackagesData = (payload) => ({
  type: PackagesActionType.SET_PACKAGES_DATA,
  payload
})
  
export const setLoadingStatus = (payload) => ({
  type: PackagesActionType.SET_LOADING_STATE,
  payload
})