
import produce from "immer";

import { LoadingStatus } from "../../types";
import { DestinationsActionType } from "./contracts/actionTypes";

const initialDestinationsState = {
  data: [],
  status: LoadingStatus.NEVER
}



export const destionationsReducer = produce((draft, action) => {
  switch(action.type){
    case DestinationsActionType.SET_DESTINATION_PACKAGES_DATA: 
      draft.data = action.payload;
      draft.status = LoadingStatus.SUCCESS;
      break;

    case DestinationsActionType.SET_LOADING_STATE:
      draft.status = action.payload;
      break;
    
    default:
      break;
  }
}, initialDestinationsState)

