import { Container } from "react-bootstrap";


const Block = ({ title, title_, children, boolean}) => {
  return (
    <Container>
      <div className={`block ${ boolean ? `pt-5` : null}`}>
        <h4 className="block__title">
          <span>{title}</span> {title_}
        </h4>
        {children}
      </div>
    </Container> 
  );
}
 
export default Block;