import produce from "immer";
import { LoadingStatus } from "../../types";
import { DestinationActionsType } from "./contracts/actionType";



const initialDestinationState = {
  data: undefined,
  status: LoadingStatus.NEVER
}
  

export const destinationReducer = produce((draft, action) => {
  switch(action.type){
    case DestinationActionsType.SET_DESTINATION_DATA: 
      draft.data = action.payload;
      draft.status = LoadingStatus.SUCCESS;
      break;

    case DestinationActionsType.FETCH_DESTINATION_DATA:
      draft.data = undefined;
      draft.status = LoadingStatus.LOADING;
      break;

    case DestinationActionsType.SET_LOADING_STATE:
      draft.status = action.payload;
      break
    
    default:
      break;
  }
}, initialDestinationState)

