import Header from "../../components/Header";
import Layout from "../Layout";

import bg from "../../assets/bg_12.jpg";
import Section from "../../components/Section";
import Footer from "../../components/Footer";
import SearchField from "../../components/SearchField";
import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { fetchPackages } from "../../store/ducks/packages/actionCreators";
import { selectPackagesData, selectPackagesStatus } from "../../store/ducks/packages/selectors";
import { LoadingStatus } from "../../store/types";
import { fetchDestinationsPackages } from "../../store/ducks/destinations/actionCreators";
import { selectDestionationsData, selectDestionationsStatus } from "../../store/ducks/destinations/selectors";

import LaunchModal from "../../components/LaunchModal";

const Home = () => {


  const packagesLoadingStatus = useSelector(selectPackagesStatus);
  const packagesData = useSelector(selectPackagesData);

  const destinationsPackages = useSelector(selectDestionationsData)
  const destinationsPackagesLoadingStatus = useSelector(selectDestionationsStatus)

  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(fetchPackages());
    dispatch(fetchDestinationsPackages())
  },[])

  

  return (
    <>
    <LaunchModal />
    <Layout bg={bg}>
      <Header bg={bg}>
        <SearchField />
      </Header>
      <Section 
        _boolean={false}
      />
      <Section
        _title="Curated"
        _span="Experiences"
        _boolean={true}
        loadingStatus={(packagesLoadingStatus !== LoadingStatus.NEVER && packagesLoadingStatus !== LoadingStatus.LOADING)}
        __package={packagesData.data.data}
        route="package_details"
      />
       <Section 
        _className="dark"
        _title="Popular"
        _span="Destinations"
        _boolean={true}
        loadingStatus={((destinationsPackagesLoadingStatus !== LoadingStatus.NEVER && destinationsPackagesLoadingStatus !== LoadingStatus.LOADING))}
        _package={destinationsPackages.data.data}
        text="The best tourist loactions across Bastar, rated and curated by travellers."
        route="destination_details"
      />
    </Layout>
    </>
  );
}
 
export default Home;