 
import produce from "immer"
import { NotificationStatus } from "../../types";
import { NotificationActionsType } from "./contracts/actionTypes";


const notificationInitialState = {
  visibal: false,
  text: undefined,
  type: NotificationStatus.NEVER
}

export const notificationReducer = produce((draft, action) => {
  
  switch(action.type){
    case NotificationActionsType.SET_NOTIFICATION_SUCCESS:
      draft.visibal = true;
      draft.text = action.payload;
      draft.type = NotificationStatus.SUCCESS;
      break;

    case NotificationActionsType.SET_NOTIFICATION_ERROR:
      draft.visibal = true;
      draft.text = action.payload;
      draft.type = NotificationStatus.ERROR;
      break

    case NotificationActionsType.CLEAR_NOTIFICATION:
      draft.visibal = false;
      draft.text = undefined;
      draft.type = NotificationStatus.NEVER;
      break
    
    default: 
      break;
   }

}, notificationInitialState)